.TitleLink{
  position: relative;
  display: inline-block;
  margin: 10px 0;
}
.TitleLink > svg{
  margin-left: 10px;
  opacity: 0;
  -webkit-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s;
  position: absolute;
  left: 100%;
  top: 55%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.TitleLink:hover > svg{
  opacity: 1;
}
@media(max-width:600px){
  .TitleLink > svg{
    display:none;
  }
}



